'use strict';

import mongoose from 'mongoose';
import {registerEvents} from './upcoming.events';

var UpcomingSchema = new mongoose.Schema({
//info about upcoming foods
title:{
 	type:String
},
thumb:{
  	type:String
},
createdAt:{
 	type:Date,
 	default:Date.now
}
});

registerEvents(UpcomingSchema);
export default mongoose.model('Upcoming', UpcomingSchema);
